.App { }

.unitStatus {
    display:-ms-flexbox;
    display:flex;
    margin-bottom: 5px;
}

.CircularProgressbar {
  /*
   * This fixes an issue where the CircularProgressbar svg has
   * 0 width inside a "display: flex" container, and thus not visible.
   *
   * If you're not using "display: flex", you can remove this style.
   */
    width: 10px;
    background-color: none;
    margin-left: 5px;
    display: block;
}

.CircularProgressbar .CircularProgressbar-path {
  stroke: orange;
  stroke-linecap: round;
}

.CircularProgressbar .CircularProgressbar-trail {
  stroke: #555555;
}

.CircularProgressbar .CircularProgressbar-text {
  display: none;
}

html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  height: 100%;
  background-color: #434343;
  /** background-image:linear-gradient(#383838, #0e0e0e); **/
}


.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.board {
    position: relative;
    margin: 0 auto;
}

.board {
    /** transform: rotateX(60deg) rotateY(0deg) rotateZ(-30deg); **/
    /** background-color: transparent;
    background-image:       linear-gradient(0deg, transparent 24%, rgba(255, 255, 255, .2) 25%, rgba(255, 255, 255, .2) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, .2) 75%, rgba(255, 255, 255, .2) 76%, transparent 77%, transparent), linear-gradient(90deg, transparent 24%, rgba(255, 255, 255, .2) 25%, rgba(255, 255, 255, .2) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, .2) 75%, rgba(255, 255, 255, .2) 76%, transparent 77%, transparent);
    **/
    height:100%;
    background-size:50px 50px;
}

.unit {
    display: inline-block;
    position: absolute;
    margin-left: -25px;
    margin-right: -25px;
    margin-top: -20px;
    margin-bottom: -20px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.item {
    display: inline-block;
    position: absolute;
}

.line {
    position: "absolute";
    opacity: 0.4;
    border-bottom-style: dashed;
    border-bottom-width: 2px;
    z-index: -10000;
}

.bullet {
    display: inline-block;
    position: absolute;
    height: 2px;
    width: 2px;
    background-color: white;
}

.bar {
    min-height: 4px;
    background-color: #555555;
    margin: 0 auto;
    margin-bottom: 1px;
}

.bar .level {
    min-height: 4px;
}

.unit-body {
    border-radius: 5px;
    background-color: black;
    padding: 4px;
    font-size: 14pt;
    display: block;
    width: 30px;
    margin: 0 auto;
    text-align: center;
    -webkit-transition: -webkit-transform 0.1s ease 0s;
    transition: -webkit-transform 0.1s ease 0s;
    -o-transition: transform 0.1s ease 0s;
    transition: transform 0.1s ease 0s;
    transition: transform 0.1s ease 0s, -webkit-transform 0.1s ease 0s;
}

.unit-red {
    color: #FF8888;
}

.unit-blue {
    color: #8888FF;
}

.time {
    padding: 10px;
    color: white;
}

.description {
    color: white;
}

.controls {
    padding: 20px;
    font-size: 18pt;
    z-index: 1000;
    position: absolute;
}

.controls button {
    color: white;
    border: 1px solid white;
    padding: 5px;
    font-size: 16pt;
    margin-right: 10px;
    text-decoration: none;
    background: black;
    margin-right: 20px;
}

.circle {
    z-index: -1;
}


